import React from 'react';
import $ from 'jquery';
import { fnSetWeatherInfo } from '../../js/weather';
import { useContext } from 'react';
import { AppContext } from '../../App';


const CompDailyLi = ({ data }) => {
  const { _weatherData } = useContext(AppContext)

  const {//fnSetWeatherInfo destructuring
    tempMin, tempMax, feelsLike,
    rain, snow, clouds, humidity, pressure, uvi, uviDesc, windDeg, windSpeed,
    mmdd, day,
    icon, desc,
  } = fnSetWeatherInfo(data, _weatherData.timezone)

  const fnAccordion = function(e){
    e.preventDefault()
    $('.comp-daily li .detail').stop().slideUp()
    $(e.currentTarget).siblings('.detail').stop().slideToggle()
    $('.comp-daily li a').not($(e.currentTarget)).removeClass('active')
    $(e.currentTarget).toggleClass('active')
  }

  return (
    <li>
      <a href='#' onClick={fnAccordion}>
        <p className='date'>
          <span><i className="fa-solid fa-calendar-days"></i> {mmdd} ({day})</span>
          <span className='desc'>'{desc}'</span>
        </p>
        <p className="info">
          <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
          <span className='temp'>
            <i className="fa-solid fa-temperature-three-quarters"></i>
            <em>{tempMin} / {tempMax} &#176;c</em>
          </span>
          {
            (rain !== 0) &&
            <span>
              <i className="fa-solid fa-umbrella"></i>
              <em>{rain}mm</em>
            </span>
          }
          {
            (snow !== 0) &&
            <span>
              <i className="fa-solid fa-snowflake"></i>
              <em>{snow}mm</em>
            </span>
          }
        </p>
        <i className="fa-solid fa-arrow-down"></i>
      </a>

      <div className="detail">
        <p>
          <span>
            <i className="fa-solid fa-temperature-arrow-up"></i>
            <b></b>
            <em>{feelsLike}<small>&#176;c</small></em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-circle-arrow-up"></i>
            <b></b>
            <em>{windDeg}<small>&#176;</small></em>
          </span>
          <span>
            <i className="fa-solid fa-wind"></i>
            <b></b>
            <em>{windSpeed}<small>m/s</small></em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-cloud"></i>
            <b></b>
            <em>{clouds}<small>%</small></em>
          </span>
        </p> 
        <p>
          <span>
            <i className="fa-solid fa-droplet"></i>
            <b></b>
            <em>{humidity}<small>%</small></em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-arrows-down-to-people"></i>
            <b></b>
            <em>{pressure}<small>hPa</small></em>
          </span>
        </p>
        <p>
          <span>
            <i className="fa-solid fa-sun-plant-wilt"></i>
            <b></b>
            <em>{uvi}, ({uviDesc})</em>
          </span>
        </p>
      </div>
    </li>
  );
};

export default CompDailyLi;