import React, { useContext } from 'react';
import { AppContext } from '../../App';
import CompLoader from '../compLoader/CompLoader';
import CompHourlyLi from './CompHourlyLi';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';

const CompHourly = () => {
  const {_weatherData} = useContext(AppContext)
  let hourlyData
  let chartArr //chartData datasets data 
  let chartData //chart data
  let chartOptions//chart options
  
  if(_weatherData){
    hourlyData = _weatherData.hourly

    //chartJs  
    chartArr = hourlyData.map(v => parseFloat((v.temp-273.15).toFixed(1))) 
    
    //chartJs 
    chartData ={
      labels: new Array(48).fill(''),
      datasets: [{
        label: 'myGraph',
        data: chartArr,
        borderWidth: 1,
        fill: false,
        borderColor: '#4378a3',
        tension:0.5,
      }]
    }

    //chartJs 
    chartOptions = {
      scales: {
        x: {
          display: false,
        },
        y: {
          display: true,
          beginAtZero: true,
          min: -50,
          max: 50,
          border:{
            display:false,
            color:'#8ed1f1',// 
            width:1,// 
            dash:[2,2],//
          },
          ticks: {// 
            display: true,
            color:'#8ed1f1',
            stepSize: 20,
          },
          grid: { 
            color: '#8ed1f1'// 
          },
        },
      },
      plugins: {
        legend: {
          display: false,//
          position: 'top',
          labels: {
            color: '#FFF'
          }
        },//legend
        datalabels: {
          display: true,
          color: "#4378a3",
          align: 'top',
        },
      },//plguins
      responsive: true,
      maintainAspectRatio: false,
    }
  }//if


  return (
    <section className='comp-hourly'>
      <div className="section-inner">
        <h2>hourlyl 48</h2>
        {
        (_weatherData)
          ?
            <div className="wrap">
              <ul>
                {
                  hourlyData.map(v=><CompHourlyLi key={v.dt} data={v}/>)
                }
              </ul>
              <div className="chart-wrap">
                <Chart type='line' data={chartData} options={chartOptions} plugins={[ChartDataLabels]} />
              </div>{/* chart-wrap */}
            </div>
          :
            <CompLoader/>
        }
      </div>{/*section-inner*/}
    </section>
  );
};

export default CompHourly;